            manager: '',
            kargeri: '',
            supervisor: '',
            accountant: '',
            employees: [],
            emp_id: null,
            employees_to_zero: [],
            currentTime: new Date().toLocaleTimeString([], {hour: '2-digit', minute: '2-digit'}),
        }
    },
    beforeCreate(){
        auth("salaryList");
    },
    created(){
        this.$http.get('/getNames').then(({data}) => {
            this.manager = data.manager;
            this.kargeri = data.kargeri;
            this.accountant = data.accountant;
        });
        this.$http.post("/getLoggedInfo").then(({data}) => {
            this.user.username = data.username;
            this.user.role = data.type;
            this.user.en_id = data.en_id || null;

            if(this.user.en_id != null){
                this.$http.post("/staff/getByEngineer/" + this.user.en_id).then(({data}) => {
                    this.staffs = data.filter(obj => obj.show_staff == '1');
                });
            } else {
                this.$http.post("/staff/getData").then(({data}) => {
                    this.staffs = data.filter(obj => obj.show_staff == '1');
                });
            }
        });
    },

    mounted() {
        setInterval(() => {
            this.currentTime = new Date().toLocaleTimeString([], {hour: '2-digit', minute: '2-digit'});
        }, 1 * 60 * 1000);
    },
   
    methods: {
        search_staff(){
            this.$http.post('/employee/getEmployeeBystaff/'+this.staff_id + '/' + this.month + '/' + this.year).then(([data])=>{
                this.employees_btn.full_name=data.full_name;
            })
        },
        search() {
            this.$http.get('/employee/getSalaryListByMonthAndYear/' + this.month + '/' + this.year + '/' + this.staff_id).then(({data}) => {
                this.salary_list = data.salary_list;
                this.employees_to_zero = data.zeros.map(obj => obj.emp_id);
                this.overWorkDayToOvertime()
            });
        },
        getSuitableSalary(item) {
            var d = new Date();
            d.setMonth(this.month - 1);
            d.setFullYear(this.year);
            const allDaysInMonth = new Date(d.getFullYear(), d.getMonth() + 1, 0).getDate();
            const numberOfFridays = this.fridays(this.year, this.month - 1);
            const days = allDaysInMonth - numberOfFridays;
            if(item.salary_type == 'Monthly') {
                item.daily_salary = Number(item.monthly_salary / days).toFixed(2);
                return Number(Number(item.monthly_salary / days).toFixed(2));
            } else {
                return (item.daily_salary);
            }
        },
        fridays(year, month) {
            var day, counter, date;
            day = 1;
            counter = 0;
            date = new Date(year, month, day);
            
            while (date.getMonth() === month) {
                if (date.getDay() === 5) { // Sun=0, Mon=1, Tue=2, etc.
                    counter += 1;
                }
                day += 1;
                date = new Date(year, month, day);
            }
            return counter;
        },
        getTotalDinar() {
            return this.filtered_data.filter(obj => obj.salary_type == 'Daily').reduce((a, b) => a + ((this.getSuitableSalary(b) * b.count_present) - Number(b.total_fine) + ((this.getSuitableSalary(b) / 8) * b.total_hour)  + b.food_money + b.transport_money + Number(b.total_expense) + Number(b.total_transport) + Number(b.total_food) + Number(b.total_loan) + Number(b.total_accomodation) + Number(b.loan_by_accomodation) + Number(b.accomodation_by_accomodation) + b.cabina_money + b.expense_money + b.fine_money + b.loan_money + b.accomodation_money + b.other_expense + b.other_minus), 0)
        },
        getTotalDollar() {
            return this.filtered_data.filter(obj => obj.salary_type == 'Monthly').reduce((a, b) => a + ((this.getSuitableSalary(b) * b.count_present) - Number(b.total_fine) + (((b.monthly_salary / 26) / 8) * b.total_hour)  + b.food_money + b.transport_money + Number(b.total_expense) + Number(b.total_transport) + Number(b.total_food) + Number(b.total_loan) + Number(b.total_accomodation) + Number(b.loan_by_accomodation) + Number(b.accomodation_by_accomodation) + b.cabina_money + b.expense_money + b.fine_money + b.loan_money + b.accomodation_money + b.other_expense + b.other_minus), 0)
        },
        lastTotalDollar() {
            return this.getTotalDollar() + (this.getTotalDinar() / (this.dollar_price || 0));
        },
        lastTotalDinar() {
            return this.getTotalDinar() + (this.getTotalDollar() * (this.dollar_price || 0));
        },
        changeNames() {
            this.$http.post('/setNames', {
                manager: this.manager,
                kargeri: this.kargeri,
                accountant: this.accountant
            }).then(() => {
                
            })
        },
        overWorkDayToOvertime() {
            for(let i = 0; i < this.salary_list.length; i++) {
                var count_present = this.salary_list[i].count_present;
                var d = new Date();
                d.setMonth(this.month - 1);
                d.setFullYear(this.year);
                const allDaysInMonth = new Date(d.getFullYear(), d.getMonth() + 1, 0).getDate();
                const numberOfFridays = this.fridays(this.year, this.month - 1);
                const days = allDaysInMonth - numberOfFridays;
                
                if(count_present > days) {
                    var overtimeDays = count_present - days;
                    this.salary_list[i].total_hour += overtimeDays * 8;
                    this.salary_list[i].count_present = days;
                }
            }
        }
    },
    computed: {
        filtered_data() {
            if(this.emp_id) {
                return this.salary_list.filter(obj => obj.emp_id == this.emp_id);
            } else {
                return this.salary_list;
            }
        },
        excel_data() {
            const arr = []
            arr.push(...this.salary_list.filter(obj => obj.salary_type == 'Monthly').map(item => {
                return {
                    'Name': item.employee_full_name,
                    'Daily': (item.salary_type == 'Monthly' ? '$' : '') + this.getSuitableSalary(item),
                    'Days': item.count_present,
                    'Hours': item.total_hour,
                    'Total Salary': (item.salary_type == 'Monthly' ? '$' : '') + (this.getSuitableSalary(item) * item.count_present),
                    'Overtime Hours': (item.salary_type == 'Monthly' ? '$' : '') + Number((((item.monthly_salary/26) / 8 ) * item.total_hour).toFixed(2)),
                    'Food': (item.salary_type == 'Monthly' ? '$' : '') + (Number(item.food_money) + Number(item.total_food)),
                    'Transport': (item.salary_type == 'Monthly' ? '$' : '') + (Number(item.transport_money) + Number(item.total_transport)),
                    'Cabina': (item.salary_type == 'Monthly' ? '$' : '') + (item.cabina_money),
                    'Expense': (item.salary_type == 'Monthly' ? '$' : '') + (Number(item.expense_money) + Number(item.total_expense)),
                    'Fine': (item.salary_type == 'Monthly' ? '$' : '') + ((item.fine_money) - item.total_fine),
                    'Loan': (item.salary_type == 'Monthly' ? '$' : '') + (Number(item.loan_money) + Number(item.total_loan) + Number(item.loan_by_accomodation)),
                    'Accomodation': (item.salary_type == 'Monthly' ? '$' : '') + (Number(item.accomodation_money) + Number(item.total_accomodation) + Number(item.accomodation_by_accomodation)),
                    'Salary per Month': (item.salary_type == 'Monthly' ? '$' : '') + (item.other_expense),
                    'Other Minus': (item.salary_type == 'Monthly' ? '$' : '') + (item.other_minus),
                    'Total (Dinar)': Math.round(Number(((this.getSuitableSalary(item) * item.count_present) + Number(((item.monthly_salary / 26) / 8 ) * item.total_hour) +   Number(item.food_money) + Number(item.transport_money) + Number(item.total_expense) + Number(item.total_transport) + Number(item.total_food) + Number(item.total_loan) + Number(item.total_accomodation) + Number(item.loan_by_accomodation) + Number(item.accomodation_by_accomodation) + Number(item.cabina_money) + Number(item.expense_money) + Number(item.fine_money) - Number(item.total_fine) + Number(item.loan_money) + Number(item.accomodation_money) + Number(item.other_expense) + Number(item.other_minus)) * this.dollar_price)).toLocaleString(),
                    'Total (Dollar)': (item.salary_type == 'Monthly' ? '$' : '') + (Number((this.getSuitableSalary(item) * item.count_present  + Number(((item.monthly_salary / 26) / 8 ) * item.total_hour) +   Number(item.food_money) + Number(item.transport_money) + Number(item.total_expense) + Number(item.total_transport) + Number(item.total_food) + Number(item.total_loan) + Number(item.total_accomodation) + Number(item.loan_by_accomodation) + Number(item.accomodation_by_accomodation) + Number(item.cabina_money) + Number(item.expense_money) + Number(item.fine_money) - Number(item.total_fine) + Number(item.loan_money) + Number(item.accomodation_money) + Number(item.other_expense) + Number(item.other_minus)).toFixed(2)))
                }
            }))

            arr.push({
                'Name': 'Total Dollar',
                'Daily': '',
                'Days': '',
                'Hours': '',
                'Total Salary': '',
                'Overtime Hours': '',
                'Food': '',
                'Transport': '',
                'Cabina': '',
                'Expense': '',
                'Fine': '',
                'Loan': '',
                'Accomodation': '',
                'Salary per Month': '',
                'Other Minus': '',
                'Total (Dinar)': '',
                'Total (Dollar)': '$' + Math.round(this.getTotalDollar())
            })

            arr.push(...this.salary_list.filter(obj => obj.salary_type == 'Daily').map((item) => {
                return {
                    'Name': item.employee_full_name,
                    'Daily': Number(item.daily_salary).toLocaleString(),
                    'Days': (item.count_present),
                    'Hours': (item.total_hour),
                    'Total Salary': Number(item.daily_salary * item.count_present ).toLocaleString(),
                    'Overtime Hours': Number((item.daily_salary / 8 ) * item.total_hour).toLocaleString(),
                    'Food': (Number(item.food_money) + Number(item.total_food)).toLocaleString(),
                    'Transport': (Number(item.transport_money) + Number(item.total_transport)).toLocaleString(),
                    'Cabina': Number(item.cabina_money).toLocaleString(),
                    'Expense': (Number(item.expense_money) + Number(item.total_expense)).toLocaleString(),
                    'Fine': Number(item.fine_money - item.total_fine).toLocaleString(),
                    'Loan': Number(Number(item.loan_money) + Number(item.total_loan) + Number(item.loan_by_accomodation)).toLocaleString(),
                    'Accomodation': Number(Number(item.accomodation_money) + Number(item.total_accomodation) + Number(item.accomodation_by_accomodation)).toLocaleString(),
                    'Salary per Month': Number(item.other_expense).toLocaleString(),
                    'Other Minus': Number(item.other_minus).toLocaleString(),
                    'Total (Dinar)': Math.round((Math.round(item.daily_salary * item.count_present)  + Math.round((item.daily_salary / 8 ) * item.total_hour)  + (item.food_money) - item.total_fine + item.transport_money + Number(item.total_expense) + Number(item.total_transport) + Number(item.total_food) + Number(item.total_loan) + Number(item.total_accomodation) + Number(item.loan_by_accomodation) + Number(item.accomodation_by_accomodation) + item.cabina_money + item.expense_money + item.fine_money + item.loan_money + item.accomodation_money + item.other_expense + item.other_minus) ).toLocaleString(),
                    'Total (Dollar)': '$' + Math.round(((item.daily_salary * item.count_present)  + ((item.daily_salary / 8 ) * item.total_hour)  + (item.food_money) - item.total_fine + item.transport_money + Number(item.total_expense) + Number(item.total_transport) + Number(item.total_food) + Number(item.total_loan) + Number(item.total_accomodation) + Number(item.loan_by_accomodation) + Number(item.accomodation_by_accomodation) + item.cabina_money + item.expense_money + item.fine_money + item.loan_money + item.accomodation_money + item.other_expense + item.other_minus)/ this.dollar_price)
                }
            }))

            arr.push({
                'Name': 'Total Dinar',
                'Daily': '',
                'Days': '',
                'Hours': '',
                'Total Salary': '',
                'Overtime Hours': '',
                'Food': '',
                'Transport': '',
                'Cabina': '',
                'Expense': '',
                'Fine': '',
                'Loan': '',
                'Accomodation': '',
                'Salary per Month': '',
                'Other Minus': '',
                'Total (Dinar)': '',
                'Total (Dollar)': '$' + Math.round(this.getTotalDinar())
            })

            arr.push({
                'Name': 'Total Dinar and Dollar',
                'Daily': '',
                'Days': '',
                'Hours': '',
                'Total Salary': '',
                'Overtime Hours': '',
                'Food': '',
                'Transport': '',
                'Cabina': '',
                'Expense': '',
                'Fine': '',
                'Loan': '',
                'Accomodation': '',
                'Salary per Month': '',
                'Other Minus': '',
                'Total (Dinar)': Math.round(this.lastTotalDinar()).toLocaleString(),
                'Total (Dollar)': '$' + Math.round(this.lastTotalDollar())
            })

            return arr
        }
    },
    watch: {
        staff_id(value) {
            this.search();
            this.employees = [];
            this.emp_id = null
            if(value){
                this.supervisor = this.staffs.find(obj => obj.st_id == value).staff_name;
                this.$http.post("/employee/getEmployeeBystaff/" + this.staff_id + '/' + this.month + '/' + this.year).then(({data}) => {
                    this.employees = data;
                });
            } else {
                this.employees = [];
            }
        },
        month() {
            this.search();
        },
        year() {
            this.search();
        },
        employees_to_zero(newArr, oldArr) {
            const newIds = newArr.filter(id => !oldArr.includes(id));
            const oldIds = oldArr.filter(id => !newArr.includes(id));
            if(newIds.length > 0 && this.staff_id && this.year && this.month) {
                this.$http.post('/employee/addZeroList', {
                    list: [{st_id: this.staff_id, month: this.month, year: this.year, emp_id: newIds[0]}]
                }).then(() => {
                    
                })
            }
            if(oldIds.length > 0 && this.staff_id && this.year && this.month) {
                this.$http.delete('/employee/deleteZeroList/' + this.year + '/' + this.month + '/' + this.staff_id + '/' + oldIds[0]).then(() => {
                    
                })
            }
            for(let i = 0; i< this.salary_list.length; i++) {
                if(this.employees_to_zero.includes(this.salary_list[i].emp_id)) {
                    this.salary_list[i].daily_salary = 0;
                    this.salary_list[i].monthly_salary = 0;
                    this.salary_list[i].total_o = 0;
                    this.salary_list[i].total_hour = 0;
                    this.salary_list[i].total_fine = 0;
                    this.salary_list[i].count_present = 0;
                    this.salary_list[i].food_money = 0;
                    this.salary_list[i].total_food = 0;
                    this.salary_list[i].total_transport = 0;
                    this.salary_list[i].total_loan = 0;
                    this.salary_list[i].total_expense = 0;
                    this.salary_list[i].total_accomodation = 0;
                    this.salary_list[i].transport_money = 0;
                    this.salary_list[i].cabina_money = 0;
                    this.salary_list[i].expense_money = 0;
                    this.salary_list[i].fine_money = 0;
                    this.salary_list[i].loan_money = 0;
                    this.salary_list[i].loan_by_accomodation = 0;
                    this.salary_list[i].accomodation_money = 0;
                    this.salary_list[i].accomodation_by_accomodation = 0;
                    this.salary_list[i].other_expense = 0;
                    this.salary_list[i].other_minus = 0;
                }
            }
        }
    },
}
</script>

